@font-face {
    font-family: Unlock;
    src: local('Unlock'), url(./assets/fonts/Unlock-Regular.ttf);
}

html {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: inherit;
}

body {
    margin: 0;
}